<template>
    <button tabindex="0" v-focus="focus" :type="htmlType" :disabled="disabled || loading" @click="$emit('click')"
            :class="[
    {'ici-button':typeClass},
    {'fms-button':!typeClass},
    sizeClass,
    typeClass,
    shapeClass,
    longshadowClass,
    {'ici-button-plain':plain,'ici-button-border':plain},
    {'ici-button-borderless':borderless},
    {'ici-button-3d':open3d},
    {'ici-button-raised':relievo},
    {'ici-button-block':long}]">
        <div class="flex flex-center">
            <div v-show="loading">
                <ici-loading size="small" :disabled="loading"></ici-loading>
            </div>
            <div>
                <slot></slot>
            </div>
        </div>
    </button>
</template>

<script>

    export default {
        name: 'a-button',
        data() {
            return {};
        },

        computed: {

            sizeClass() {
                var size = '';
                switch (this.size) {
                    case 'tiny':
                        size = 'ici-button-tiny';
                        break;
                    case 'small':
                        size = 'ici-button-small';
                        break;
                    case 'large':
                        size = 'ici-button-large';
                        break;
                    case 'jumbo':
                        size = 'ici-button-jumbo';
                        break;
                    case 'giant':
                        size = 'ici-button-giant';
                        break;
                }
                return size;
            },
            typeClass() {
                var type = '';
                switch (this.type) {
                    case 'primary':
                        type = 'ici-button-primary';
                        break;
                    case 'info':
                        type = 'ici-button-action';
                        break;
                    case 'warning':
                        type = 'ici-button-highlight';
                        break;
                    case 'error':
                        type = 'ici-button-caution';
                        break;
                    case 'ici':
                        type = 'ici-button-royal';
                        break;
                    default :
                        type = this.type
                }
                return type;
            },
            shapeClass() {
                var shape = '';
                switch (this.shape) {
                    case 'rounded':
                        shape = 'ici-button-rounded';
                        break;
                    case 'pill':
                        shape = 'ici-button-pill';
                        break;
                    case 'square':
                        shape = 'ici-button-square';
                        break;
                    case 'box':
                        shape = 'ici-button-box';
                        break;
                    case 'circle':
                        shape = 'ici-button-circle';
                        break;
                }
                return shape;
            },
            longshadowClass() {
                var longshadow = '';
                switch (this.longshadow) {
                    case true:
                    case 'right':
                        longshadow = 'ici-button-longshadow-right';
                        break;
                    case 'left':
                        longshadow = 'ici-button-longshadow-left';
                        break;
                }
                return longshadow;
            }
        },
        props: {
            focus: Boolean,
            disabled: Boolean,
            //原生的type
            htmlType: {
                type: String,
                default: 'button'
            },
            loading: Boolean,
            //按钮大小
            size: {
                type: String,
                default: ''
            },
            //按钮主题类型大小
            type: {
                type: String,
                default: ''
            },
            //按钮形状
            shape: {
                type: String,
                default: ''
            },
            longshadow: {
                type: [Boolean, String]
            },
            //按钮是否是空心的
            plain: Boolean,
            //按钮是否是要边框
            borderless: Boolean,
            //开启3D效果
            open3d: Boolean,
            //开启浮雕
            relievo: Boolean,
            //开启100%长度
            long: Boolean,
        },
        mounted() {
        },
        beforeDestroy() {
        },
        methods: {},
        components: {}
    };
</script>

